.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.button {
  display: block;
  font-size: 35px;
  line-height: 40px;
  padding: 5px 40px 15px;
  border-radius: 30px;
  background-image: linear-gradient(to bottom, #ffe402 0%, #ff0404 100%);
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: center; */
}

.button:hover {
  background-image: linear-gradient(to bottom, #6a4eff 0%, #452ebf 100%);
}

.button__text {
  color: #fff;
}

.link {
  text-decoration: none;
}

@font-face {
  font-family: "PariMatchRegular";
  src: url("../fonts/Pari-Match-Regular.eot");
  src: url("../fonts/Pari-Match-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Pari-Match-Regular.woff2") format("woff2"), url("../fonts/Pari-Match-Regular.woff") format("woff"), url("../fonts/Pari-Match-Regular.ttf") format("truetype"), url("../fonts/Pari-Match-Regular.svg#PariMatchRegular") format("svg");
  /* iOS 4.1- */
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html {
  scroll-behavior: smooth;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after {
  content: "";
  content: none;
}

q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  outline: none;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.page {
  position: relative;
  margin: 0;
  padding: 0;
}

.page_theme {
  font: 14px / 2.14 "PariMatchRegular", Arial, sans-serif;
  color: #000;
  background: #fff;
  min-width: 100%;
  min-height: 100vh;
}

.layout {
  width: 100%;
  height: 100%;
  background: #040506;
}

.layout__wrap {
  box-shadow: inset 0px 0px 25px 0px rgba(0, 0, 0, 0.75);
  max-width: 1920px;
  width: 100%;
  min-height: 100%;
  margin: auto;
  background: url("../img/bg_main.png") 0 0 no-repeat;
  padding: 0 30px;
  box-sizing: border-box;
  overflow: hidden;
}

.header {
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.header__inner {
  padding: 30px 0;
  text-align: center;
}

.header__logo {
  display: block;
  margin: auto;
  width: 600px;
  height: 85px;
  margin-bottom: 160px;
}

.header__text {
  font-size: 142px;
  line-height: 140px;
  font-weight: bold;
  background: -webkit-linear-gradient(#ffe402, #ff0404);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  /*  font-size: 72px;
    background-image: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: relative; */
  /*&:after {      
       background: none;
      content: attr(data-text);
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      text-shadow: 5px 5px 5px red;

      z-index: -1; 
    }*/
}

.header__text:after {
  width: 100%;
  content: attr(data-text);
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
  top: 0;
  z-index: -1;
}

.header__date {
  font-size: 80px;
  color: #fff;
  line-height: 80px;
  padding: 20px;
}

.header__go {
  width: 300px;
  margin: auto;
}

.header__scroll {
  display: flex;
  justify-content: center;
  padding-top: 60px;
}

.header__scrollLink {
  display: flex;
  justify-content: center;
}

.logo {
  width: 100%;
}

.logo__link {
  display: block;
}

.logo__img {
  margin: auto;
}

html,
body {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 24px;
    padding: 5px 30px 15px;
  }

  .layout__wrap {
    padding: 0 15px;
  }

  .header__logo {
    width: 500px;
    margin-bottom: 60px;
  }

  .logo__img {
    width: 80%;
  }
}

@media screen and (max-width: 640px) {
  .layout__wrap {
    background-image: url("../img/bg_main_mob.png");
    background-size: cover;
    background-position: 0 -40px;
  }

  .header__logo {
    width: 300px;
    margin-bottom: 30px;
  }

  .header__text {
    font-size: 80px;
    line-height: 80px;
  }

  .header__date {
    font-size: 40px;
  }

  .header__go {
    padding-top: 100px;
  }

  .header__scroll {
    padding-top: 40px;
  }

  .header__scrollImg {
    width: 70%;
  }
}

@media screen and (max-width: 540px) {
  .header__logo {
    font-size: 60px;
    margin-bottom: 10px;
  }

  .header__text {
    font-size: 60px;
    line-height: 60px;
  }

  .header__go {
    padding-top: 15px;
    width: 200px;
  }
}